html {
  background: linear-gradient(to left, #181a1b, #111613);
}

body {
  margin: 0;
}

.wrapper {
  padding: 0;
  margin: 0;
}

.header {
  padding: 0 10px;
  padding-top: 20px;
}

.hat {
  display: flex;
  border-bottom: 1px solid white;
}

.logoImg {
  height: 50px;
  width: 80px;
}

.logo {
  padding-left: 10px;
  color: white;
  font-family: sans-serif;
  font-size: 24px;
}

.nav {
  padding-top: 40px;
  width: 600px;
  margin-left: auto;
  display: flex;
  justify-content: space-around;
}

.navItem {
  text-decoration: none;
  color: white;
  font-family: sans-serif;
  font-size: 15px;
  transition: all 0.3s ease-out 0.1s;
}

.navItem:hover {
  color: rgb(197, 38, 38);
}

.btns {
  width: 300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  padding: 20px 0;
}

.btn {
  text-decoration: none;
  font-weight: 400;
  font-family: sans-serif;
  border: 1px solid rgb(197, 38, 38);
  padding: 10px;
  color: rgb(197, 38, 38);
  transition: all 0.3s ease-out 0.1s;
}

.btn:hover {
  color: blueviolet;
  border: 1px solid blueviolet;
}

.mainImg {
  height: auto;
  width: 100vw;
}

.textSection {
  color: white;
  text-align: center;
  font-family: sans-serif;
  width: 785px;
  margin: 0 auto;
}

.textSectionTitle {
  margin-bottom: 20px;
  font-size: 80px;
  font-weight: normal;
  line-height: 1.6;
}

.textSectionSpan {
  line-height: 3;
  font-size: 22px;
  font-weight: normal;
}

.textSectionP {
  line-height: 1.6;
  margin-bottom: 70px;
}

.optionsSection {
  background: linear-gradient(to left, #232b30, #1e2521);
  padding-top: 20px;
}

.list {
  width: 300px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

.listItem {
  margin-bottom: 40px;
}

.title {
  font-family: sans-serif;
  text-align: center;
  font-size: 24px;
  font-weight: 800;
  color: rgb(197, 38, 38);
}

.time {
  background-image: url('./images/tires.jpg');
  padding: 50px;
}

.timeInfo {
  color: white;
  font-weight: 600;
  font-size: 20;
  font-family: sans-serif;
  text-align: center;
  line-height: 1.6;
}

.contactSection {
  padding-top: 20px;
}

.contacts {
  margin-top: 100px;
  margin-bottom: 100px;
  text-align: center;
  color: white;
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
}

.contactListItem {
  width: 30vw;
  display: flex;
  flex-direction: column;
}

.icon {
  display: block;
  height: 50px;
  width: 50px;
  margin: 0 auto;
}

.contactTitle::before {
  display: block;
  content: '';
  height: 3px;
  width: 50%;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: rgb(175, 238, 50);
}

.link {
  text-decoration: none;
  color: rgb(197, 38, 38);
  transition: all 0.3s ease-out 0.1s;
}

.link:hover {
  color: blueviolet;
}

.map {
  height: 150px;
  width: 300px;
  line-height: 150px;
  margin: 0 auto;
  border: 1px solid rgb(34, 34, 124);
  text-align: center;
  color: rgb(34, 34, 124);
}

.footer {
  margin-top: 20px;
  display: flex;
  width: 100%;
  justify-content: center;
}

@media (min-width: 768px) {
  .header {
    background: linear-gradient(to left, #181a1b, #111613);
    position: fixed;
    z-index: 2;
    width: 90%;
    padding: 0 80px;
    padding-top: 20px;
    justify-content: start;
  }

  .logoImg {
    height: 60px;
    width: 100px;
  }

  .logo {
    font-size: 30px;
  }

  .btns {
    width: 600px;
  }

  .mainImg {
    padding-top: 176px;
    width: 98.9vw;
  }

  .list {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .listItem {
    width: 28%;
  }

  .time {
    padding: 150px;
  }
}
